import React, { useState, useEffect } from 'react';
import Menu from './Menu'; 



const scrollToDescription = () => {
  document.querySelector('.DescContainerMain').scrollIntoView({ behavior: 'smooth' });
};

const QualityControl = ({ backgroundImage }) => {
  const [imagePath, setImagePath] = useState(null);
  const [imageBlobUrl, setImageBlobUrl] = useState(null); 
  const [prediction, setPrediction] = useState(null);
  const [confidence, setConfidence] = useState(null);
  useEffect(() => {
    if (imagePath) {
        fetch(imagePath)
            .then(res => res.blob())
            .then(blob => {
                if (imageBlobUrl) {
                    // Free old Blob URL
                    URL.revokeObjectURL(imageBlobUrl);
                }
                // Create new Blob URL
                setImageBlobUrl(URL.createObjectURL(blob));
            })
            .catch(console.error);
    }

    // Cleaning function
    return () => {
        if (imageBlobUrl) {
            URL.revokeObjectURL(imageBlobUrl);
        }
    };
  }, [imagePath, imageBlobUrl]); 

  const selectRandomImage = async () => {
    try {
        const response = await fetch(`${process.env.REACT_APP_API_URL}/image-list`);
        const data = await response.json();
        const imageList = data.images;

        const randomIndex = Math.floor(Math.random() * (imageList.length - 1));
        const randomImageName = imageList[randomIndex];

        const fullImagePath = `${process.env.PUBLIC_URL}/Dataset/Metal_piece_default_detection/${randomImageName}`;
        setImagePath(fullImagePath);
        setPrediction(null);
        setConfidence(null);
    } catch (error) {
        console.error("Erreur lors de la rcupration de la liste des images:", error);
    }
  };

  const executePrediction = async () => {
    if (!imagePath) return;

    try {
        const imageResponse = await fetch(imagePath);
        const imageBlob = await imageResponse.blob();

        const formData = new FormData();
        formData.append('file', imageBlob, 'randomImage.jpg'); 

        const response = await fetch(`${process.env.REACT_APP_API_URL}/predict_QualityControl`, {
            method: 'POST',
            body: formData,
        });

        const data = await response.json();

        setPrediction(data.class);
        setConfidence(data.confidence);
    } catch (error) {
        console.error("Erreur lors de la demande de prdiction:", error);
    }
  };

  return (
    <div>
      <div className="BGImageContainer" style={{ backgroundImage: `url(${backgroundImage})` }}>
        <div>
          <Menu />
        </div>
        <div className="FirstContainer">
          <div className='CntFirstContainer'>
            <div className="QualityControlPanelContainer">
              <div className="Info_PhotoContainer">
                <div className="container PredictionContainer">
                    {prediction ? (
                        <>
                            <div><h3>Class : {prediction}</h3></div>
                            <div><h4>Confidence : {(confidence * 100).toFixed(2)}%</h4></div>
                        </>
                    ) : (
                        <div className="placeholder">No Prediction Available Yet</div>
                    )}
                </div>
                <div className="container ImageContainer">
                  {imagePath ? <img src={imagePath} alt="Randomly selected picture" style={{ maxWidth: '100%', maxHeight: '300px' }} /> : <div className="placeholder">No Image Selected</div>}
                </div>
              </div>
              <div className="container ButtonsContainer">
                <button className="black-button" onClick={selectRandomImage}>Select a random image</button>
                <button className="gold-button" onClick={executePrediction} disabled={!imagePath}>Predict class</button>
              </div>
            </div>
            <div className="DescRefContainer" onClick={scrollToDescription}>
              <p className="Clickable-text">Learn more about this project</p>
              <img src={`${process.env.PUBLIC_URL}/DescLogo.png`} alt="Description Logo" className="Clickable-logo" />
            </div>
          </div>
        </div>
      </div>
        <div className="DescContainerMain">
          <div className="DescContainer">
            <h6>Description</h6>
            <h5>This project was created using a Kaggle dataset to simulate a supply chain management issue. The purpose of this model is to execute quality control tests on production. <br /><br /><br />
              
              More details : This model being an image classifier, there was no real need for data cleaning. Hence, the first step was to normalize data after loading it. A train-test split method was then used 
              to decompose the dataset into three sub-sets: train, validation and test (using 80,10 and 10 percent respectively of the original dataset). The third step involved using data augmentation on 
              training dataset to diversify and enhance it for training. Concerning the model, a lot of different architectures were tested and judged by classification reports using multiple combinations 
              of hyperparameters (with the help of a tuner). The basis was, of course, a CNN, and so convolutional and pooling layers were used before flattening for transmitting it to a combination of hidden and 
              dropout layers. Validation dataset was used to test training steps after each epoch and history of metrics was used to print training graphs. Final prediction tests were also conducted before saving the 
              model. A mix of the testing dataset and augmented images are accessible here to perform real life conditions testing predictions.<br /><br />
              The dataset that was used from Kaggle can be found <a href="https://www.kaggle.com/datasets/ravirajsinh45/real-life-industrial-dataset-of-casting-product/data" className="KaggleLink" target="_blank">here</a>
            </h5>
          </div>
        </div>
    </div> 
  );
};

export default QualityControl;



