import React, { useState } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import { Drawer, List, CssBaseline, Toolbar, ListItem, ListItemIcon, ListItemText, IconButton, Typography, Box, Divider } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import CancelIcon from '@mui/icons-material/Cancel'; // Import Cancel icon
import HomeIcon from '@mui/icons-material/Home';
import AssessmentIcon from '@mui/icons-material/Assessment';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import MuiAppBar from '@mui/material/AppBar';
import { styled } from '@mui/material/styles';
import logo from './Multimedia/icon.png';

const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Menu = () => {
  const [open, setOpen] = useState(false);
  const location = useLocation();

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar position="fixed" open={open} sx={{ backgroundColor: '#000' }}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{
              marginRight: '36px',
              ...(open && { display: 'none' }),
            }}
          >
            <MenuIcon />
          </IconButton>
          {/* Add your logo here */}
          <img src={logo} alt="Logo" style={{ height: '48px', width: '48px', marginRight: '10px' }} />
          <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1, fontFamily: '"Raleway", sans-serif', fontSize: '40px',  color:'#AAAAAA'}}>
            Yoan Bacus
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
            backgroundColor: '#1c1c1c'
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
        <Toolbar>
          <IconButton onClick={handleDrawerClose} sx={{ marginLeft: 'auto', color: 'white'  }}> {/* Adjusted for right corner */}
            <CancelIcon /> {/* Changed to Cancel icon */}
          </IconButton>
        </Toolbar>
        <Divider />
        <List sx={{ color: 'white' }}>
          {[{ text: 'Home', icon: <HomeIcon sx={{ color: 'white' }} />, link: '/' },
            { text: 'Quality Control', icon: <AssessmentIcon sx={{ color: 'white' }} />, link: '/QualityControl' },
            { text: 'Gold Closing Price', icon: <AttachMoneyIcon sx={{ color: 'white' }} />, link: '/GoldClosingPrice' }]
            .map((item, index) => (
              <ListItem button key={item.text} component={RouterLink} to={item.link} selected={location.pathname === item.link}>
                <ListItemIcon>{item.icon}</ListItemIcon>
                <ListItemText primary={item.text} />
              </ListItem>
            ))}
        </List>
      </Drawer>
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          transition: theme => theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
          }),
          marginLeft: `-${drawerWidth}px`,
          ...(open && {
            transition: theme => theme.transitions.create('margin', {
              easing: theme.transitions.easing.sharp,
              duration: theme.transitions.duration.enteringScreen,
            }),
            marginLeft: 0,
          }),
        }}
      >
        <Toolbar />
        {/* Your Page Content Here */}
      </Box>
    </Box>
  );
};

export default Menu;
