import React, { useState} from 'react';
import Menu from './Menu'; 



const scrollToDescription = () => {

    document.querySelector('.DescContainerMain').scrollIntoView({ behavior: 'smooth' });
};

const GoldClosingPrice = ({ backgroundImage }) => {
  const [openPrice, setOpenPrice] = useState('');
  const [highPrice, setHighPrice] = useState('');
  const [lowPrice, setLowPrice] = useState('');
  const [predictedClosePrice, setPredictedClosePrice] = useState(null);

  const executePrediction = async () => {
      if (!openPrice || !highPrice || !lowPrice) return;

      try {
          const payload = {
              Open: parseFloat(openPrice),
              High: parseFloat(highPrice),
              Low: parseFloat(lowPrice),
          };
          
          const response = await fetch(`${process.env.REACT_APP_API_URL}/predict_GoldClosingPrice`, {
              method: 'POST',
              headers: {
                  'Content-Type': 'application/json',
              },
              body: JSON.stringify(payload),
          });

          const data = await response.json();

          setPredictedClosePrice(data.predictedClose);
      } catch (error) {
          console.error("Erreur lors de la demande de prdiction:", error);
      }
  };
    return (
      <div>
        <div className="BGImageContainer" style={{ backgroundImage: `url(${backgroundImage})` }}>
          <div>
            <Menu />
          </div>
          <div className="FirstContainer">
            <div className='CntFirstContainer'>
              <div className="QualityControlPanelContainer">
                <div className="GoldPredictionResultContainer">
                  {predictedClosePrice ? (
                    <div><h3>Predicted Closing Price: {predictedClosePrice.toFixed(2)}&euro;</h3></div>
                  ) : (
                    <div className="placeholder">No Prediction Available Yet</div>
                  )}
                </div>
                <div className="InputsContainer">
                  <div className="GoldClosingPriceFormEl">
                    <label htmlFor="OpenPrice">Openning Price</label>
                    <input
                        type="number"
                        value={openPrice}
                        onChange={(e) => setOpenPrice(e.target.value)}
                        placeholder="Openning Price"
                        id="OpenPrice"
                        name="OpenPrice"
                    />
                  </div>
                  <div className="GoldClosingPriceFormEl">
                    <label htmlFor="HighPrice">Highest Price</label>
                    <input
                        type="number"
                        value={highPrice}
                        onChange={(e) => setHighPrice(e.target.value)}
                        placeholder="Highest Price"
                        id="HighPrice"
                        name="HighPrice"
                    />
                  </div>
                  <div className="GoldClosingPriceFormEl">
                    <label htmlFor="LowPrice">Lowest Price</label>
                    <input
                        type="number"
                        value={lowPrice}
                        onChange={(e) => setLowPrice(e.target.value)}
                        placeholder="Lowest Price"
                        id="LowPrice"
                        name="LowPrice"
                    />
                  </div>
                  <button className="gold-buttonGldpr" onClick={executePrediction}>
                      Predict Closing Price
                  </button>  
                </div>
              </div>
              <div className="DescRefContainer" onClick={scrollToDescription}>
                <p className="Clickable-text">Learn more about this project</p>
                <img src={`${process.env.PUBLIC_URL}/DescLogo.png`} alt="Logo" className="Clickable-logo" />
              </div>
            </div>
          </div>
        </div>
        <div className="DescContainerMain">
          <div className="DescContainer">
            <h6>Description</h6>
            <h5>This project was created using a Kaggle dataset to simulate a virtual asset manager helper. The purpose of this model is to predict the price of Gold using little information. </h5>
          </div>
        </div>
      </div> 
    );
  };

export default GoldClosingPrice;